import React, {Component} from 'react';

import Questions from './Questions';
import Result from './Result';

import firebase from './firebase';

class Quiz extends Component {

    constructor(props) {
        super(props);
        this.state = {
            fbkeys: '',
            result: '',
            description: ''
        }
    }

    mode = (array) => {
        if(array.length == 0) {
            return null;
        }
        var modeMap = {};
        var maxEl = array[0], maxCount = 1;
        for(var i = 0; i < array.length; i++) {   
            var el = array[i];
            if(modeMap[el] == null) {
                modeMap[el] = 1;
            }
            else {
                modeMap[el]++;  
            }
            if(modeMap[el] > maxCount) {
                maxEl = el;
                maxCount = modeMap[el];
            }
        }
        return maxEl;
    }
    



  endQuiz = (results) => {
    let result = this.mode(results);
    console.log(result);

    const resultsRef = firebase.database().ref(`Results/${result}`);
    let description = '';

    resultsRef.once("value", (snap) => {
        description = snap.val().description;
        console.log(description);    
        this.setState({
            result: result,
            description: description
        });
    });
    this.scrollToBottom();
  }

  scrollToBottom = () => {
    this.quizEnd.scrollIntoView({ behavior: "smooth" });
  }
  

    render() {
        return (
            <div>
                <Questions endQuiz={this.endQuiz} />
                    { this.state.description !== ''
                    ? 
                    <Result
                    result={this.state.result}
                    description={this.state.description}
                    ref={(el) => { this.Result = el; }}></Result>
                    : <div></div> }
                
                <div
                    style={{ float:"left", clear: "both" }}
                    ref={(el) => { this.quizEnd = el; }} >
                </div>
            </div>
        );
    }
}

export default Quiz;