import React, {Component} from 'react';

import firebase from './firebase';

const style = {
    hover: {
        boxShadow: "0px 0px 3px 3px #ededed",
        fontWeight: "500",
        transition: "250ms",
        cursor: "pointer"
    },
    unhover: {
        transition: "250ms"
    },
    clicked: {
        backgroundColor: "#0000fe",
        fontWeight: "500",
        color: "#ffffff"
    },
    unclicked: {
        backgroundColor: "#ffffff",
        color: "#6f6f6f"
    },
    image: {
        float: 'center',
        width: "90%",
        display: "flex",
        textAlign: "center",
        itemAlign: "center"
      }
}

class Answer extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            fbkey: this.props.fbkey,
            index: this.props.index,
            image: this.props.image,
            response: this.props.response,
            result: this.props.result,
            hover: 'unhover',
            selected: ''
        }
    }

    onMouseEnterHandler = () => {
        this.setState({
            hover: 'hover'
        });
    }

    onMouseLeaveHandler =  () => {
        this.setState({
            hover: 'unhover'
        });
    }

      render () {
          let image;
          if (this.state.image) {
              image = "image"
              console.log(image);
          }
          else {
              image = ""
          }
          return (
            <div className={"Answer " + image}
                style={{...style[this.state.hover], ...style[this.props.style]}}
                onMouseOver={this.onMouseEnterHandler.bind(this)} 
                onMouseLeave={this.onMouseLeaveHandler.bind(this)}
                onClick={this.props.onClick}
                >       
                
                { this.state.image
                ? 
                    <img style={style['image']} src={this.state.image} alt="" />    

                : <p></p>}

                {this.state.response
                ? <p>{this.state.response}</p>
                : <p></p> }


            </div>
          );
      }
}

export default Answer;