import React, {Component} from 'react';

import Answers from './Answers';

import firebase from './firebase';

class Question extends Component {
    constructor(props) {
        super(props);
        this.state = {
            fbkey: this.props.fbkey,
            question: '',
            result: ''
        }
    }

    componentDidMount(){
        const questionRef = firebase.database().ref(`Questions/${this.state.fbkey}/question`);
        questionRef.once("value", (snap) => {
            let question = snap.val();
          
          this.setState({
            question: question
          });
        })
      }

    render() {
        return (
            <div>
                <h2>{this.state.question}</h2>
                <Answers fbkey={this.state.fbkey} 
                        handleResults={this.props.handleResults}
                        index={this.props.index} ></Answers>
            </div>
        );
    }
        
}

export default Question;