import React, {Component} from 'react';

import Question from './Question';

import firebase from './firebase';

class Questions extends Component {

    constructor(props) {
        super(props);
        this.state = {
            quizSize: 5, //pigeonhole principle. make the person choose one!!
            fbkeys: '',
            results: []
        }
    }

    componentDidMount(){
    const questionsRef = firebase.database().ref(`Questions`);

    questionsRef.once("value", (snap) => {
      let fbkeys = [];
        Object.keys(snap.val()).forEach((key)=>{
            fbkeys.push(key);
        });
        console.log(fbkeys);

      this.setState({
        fbkeys: fbkeys
      });
    })
  }

  handleResults = (index, result) => {
    let results = [...this.state.results];
    results[index] = result;

    console.log(results);
    this.setState({
        results: results
    })

    if((results.length === this.state.quizSize)&&(!results.includes(undefined))) {
        this.props.endQuiz(results);
    }
  }

    render() {
        return (
            <div>
                { this.state.fbkeys
                ? 
                (Object.keys(this.state.fbkeys).map((index) =>
                <Question fbkey={this.state.fbkeys[index]}
                            handleResults={this.handleResults}
                            index={index}
                         />
              ))
              : <h2>Loading...</h2>           
            }
            </div>
        );
    }
}


export default Questions;