import React, {Component} from 'react';

import Answer from './Answer';

import firebase from './firebase';

const style = {
    clicked: {
        backgroundColor: "#0000fe",
        fontWeight: "500",
        color: "#ffffff"
    },
    notclicked: {
        backgroundColor: "#ffffff",
        color: "#dddddd"
    }
}

class Answers extends Component {

    constructor(props) {
        super(props);
        this.state = {
            qindex: this.props.index,
            fbkey: this.props.fbkey,
            answers: '',
            //images: false,
            response: '', // this is the value of the response the user gives
            result: '',
            selectedIndex: ''
        }
    }

    onClick = (index) => {
        const response = this.state.answers[index].response;
        const result = this.state.answers[index].result;
        const selectedIndex = index;
        
        this.setState({ 
            response: response,
            result: result,
            selectedIndex: selectedIndex
        });

        this.props.handleResults(this.state.qindex, result);

    }

    componentDidMount(){
    const answersRef = firebase.database().ref(`Questions/${this.state.fbkey}/answers`);

    answersRef.once("value", (snap) => {
      let answers = [];
      snap.forEach((i)=>{
        answers.push(i.val());
      });
      
      this.setState({
        answers: answers
      });
    })
  }

    render() {
        return Object.keys(this.state.answers).map((index) => {
            let style;
            if (this.state.selectedIndex === index) {
                style = "clicked"
            }
            else if (this.state.selectedIndex === '') {
                style = ""
            }
            else {
                    style = "unclicked"
            }

            return <Answer 
                index={index}
                response={this.state.answers[index].response}
                result={this.state.answers[index].result}
                image={this.state.answers[index].image}
                fbkey={this.props.fbkey}
                onClick={() => this.onClick(index)}
                style={style}
                />
          }  );
    }
}


export default Answers;